.x-wrapper {
  display: flex;
  width: 100%;

}

.x-content {
  display: flex;
  width: 100%;
  height: 40px;
}

.origin {
  width: 5%;
}

.periods {
  display: flex;
  width: 100%;
  justify-content: center;
}

.period {
  display: flex;
  text-align: center;
  flex-direction: column;
  width: 10%;
  margin: 0 .5em;
  z-index: 25;
  height: 50px;

}

.label-content {
  width: 95%;
  height: 60px;
  display: flex;
  flex-direction: column;
}

.x-title {
  text-align: center;
  margin-top: 5px;
}

.x-title h4 {
  margin: 0;
}

.nav-history-wrapper {
  display: flex;
  justify-content: center;
  margin: 50px 0;
}

.nav-history {
  width: 70%;
  max-width: 500px;
  display: flex;
  height: 40px;
  padding: 10px;
  background: #343a40;
  color: #fff;
