}

.chart-dropdown {
  width: 85px;
  background: #b2e1ff;
}

.form-row {
  margin: 20px 0 !important;
}
.form-display {
  width: 80%;
  max-width: 520px;
}
.form-group {
  margin-bottom: 15px;
}

.form-control {
  display: block;
  width: 100%;
  height: 40px;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.428;
  color: #555;
  border: 1px solid #ccc;
